import { Box, Button, TextField, Typography } from '@mui/material'
import React, { useState } from 'react'
import axios from 'axios'
import { useDispatch } from 'react-redux'
import { authActions } from '../store'
import { useNavigate } from 'react-router-dom'

function Auth() {
  const navigate = useNavigate()
  const dispatch = useDispatch()

  const [isSignup, setIsSignup] = useState(false)

  const [inputs, setInputs] = useState({
    name: "", email: "", password: ""
  })









  const handleChange = (e) => {

    setInputs((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value
    }))


  }


  const sendRequest = async (type = 'login') => {
    const res = await axios.post(`http://localhost:3000/api/user/${type}`, {
      name: inputs.name, email: inputs.email, password: inputs.password

    }).catch((err)=>alert("Login failed"))

    const data = await res.data;
    return data;
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    console.log(inputs)

    if (isSignup) {
      sendRequest("signup").then((data) => localStorage.setItem("userId", data.user._id)).then(() => dispatch(authActions.login())).then(() => navigate("/blogs")).then(data => console.log(data))
    } else {
      sendRequest().then((data) => localStorage.setItem("userId", data.user._id)).then(() => dispatch(authActions.login())).then(() => navigate("/blogs")).then(data => console.log(data))
    }
  }



  return (
    <div>

      <form onSubmit={handleSubmit}>
        <Box display='flex' flexDirection={'column'} alignItems={'center'} justifyContent={'center'} maxWidth={400} boxShadow={'10px 10px 20px #ccc'} padding={3} margin={'auto'} marginTop={5} borderRadius={5} >
          <Typography variant='h4' padding={3} textAlign={'center'} >{isSignup ? "Signup" : "Login"}</Typography>


          {isSignup && <TextField onChange={handleChange} name='name' value={inputs.name} placeholder='Name' sx={{ margin: 1 }} />
          }        <TextField onChange={handleChange} name='email' value={inputs.email} type='email' placeholder='Email' sx={{ margin: 1 }} />
          <TextField onChange={handleChange} name='password' value={inputs.password} type='password' placeholder='Password' sx={{ margin: 1 }} />

          <Button type='submit' variant='contained' sx={{ borderRadius: 3, marginTop: 1 }}  >Submit</Button>
          <Button onClick={() => setIsSignup(!isSignup)} sx={{ marginTop: 3, borderRadius: 4 }} >{isSignup ? "Login here" : "create an account"}</Button>


        </Box>
      </form>
    </div>
  )
}

export default Auth