import { Avatar, Box, Card, CardContent, CardHeader, CardMedia, IconButton, Typography } from '@mui/material'
import { red } from '@mui/material/colors'
import React, { useEffect } from 'react'
import ModeEditOutLineIcon from '@mui/icons-material/ModeEditOutline';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';





function Blog({title,description,imageURL,userName,isUser,id}) {
  
  
  const navigate=useNavigate()


  
  const handleEdit=(e)=>{
    navigate(`/myBlogs/${id}`)

  }


const deleteRequest=async()=>{
  const res=await axios.delete(`http://localhost:3000/api/blog/${id}`).catch(err=>console.log(err))
const data=await res.data;
return data;

}




  const handleDelete=()=>{
    deleteRequest().then(()=>navigate('/')).then(()=>navigate('/blogs'))
  }
  




  console.log(title,isUser)
  return (
    <div>
      
        <Card   sx={{width:'40%',margin:'auto',mt:2,padding:2,boxShadow:'5px 5px 10px #ccc',":hover":{
            boxShadow:'10px 10px 20px #ccc'

        }}}>
            
          { isUser && (
            <Box display='flex'>
              <IconButton  onClick={handleEdit}  sx={{marginLeft:'auto'}} ><ModeEditOutLineIcon/></IconButton>
              {/* <IconButton color='error' onClick={handleDelete}  ><DeleteForeverIcon/></IconButton> */}
            </Box>
          )}
          <Link style={{textDecoration:'none'}} >
            <CardHeader  avatar={<Avatar sx={{bgcolor:red[500]}} aria-label="recipie"  > {userName.charAt(0)}</Avatar>} title={title} subheader="September 14,2045" />
            <CardMedia   component={'img'} height={'194'} image={imageURL} alt='image'  /> 
            <CardContent>
            <hr /> <br />
                <Typography variant='body2' color={'text.secondary'} >
                  <b> {userName}</b> {":"}    {description}</Typography>
            </CardContent>
            </Link>
         
        </Card>
    </div>
  )
}

export default Blog