import React, { useState } from 'react'
import {AppBar, Box, Button, Tab, Tabs, Toolbar, Typography} from '@mui/material'
import {Link, useNavigate} from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux';
import { authActions } from '../store';



function Header() {
const navigate=useNavigate()
const [value,setValue]=useState();
const isLoggedIn=useSelector((state)=>state.isLoggedIn);

const id =localStorage.getItem("userId")
const dispatch=useDispatch()

const handleLogout=()=>{
  localStorage.removeItem("userId")
  navigate('/')
  
}

  return (
    <AppBar position='sticky' sx={{background:" rgba(214,0,0,)"}}>
      <Toolbar>
        <Typography variant='h5'>BlogApp</Typography>
        { id &&  <Box display='flex' marginLeft={'auto'}  marginRight={'auto'}>
          <Tabs value={value} onChange={(e,val)=>setValue(val)}  textColor='white' indicatorColor='	rgb(255, 191, 0)'>
            <Tab  LinkComponent={Link} to='/blogs'   label="All Blogs" />
            <Tab    LinkComponent={Link} to='/myBlogs'  label="My Blogs" />
            <Tab    LinkComponent={Link} to='/blogs/add'  label="Add Blog" />
          </Tabs>


        </Box>}
        <Box display="flex" marginLeft="auto">
          {
            !id &&
            <>
            <Button   LinkComponent={Link} to='/auth'  variant="contained" sx={{margin:1,borderRadius:10}} color='warning' >Login</Button>
            <Button   LinkComponent={Link} to='/auth'  variant="contained" sx={{margin:1,borderRadius:10}} color='warning' >Signup</Button>
          </> }
         
         { id && <Button  onClick={handleLogout} LinkComponent={Link} to='/auth'   variant="contained" sx={{margin:1,borderRadius:10}} color='warning' >Logout</Button>}
        </Box>
      </Toolbar>
    </AppBar>
  )
}

export default Header