import { Box, Button, InputLabel, TextField, Typography } from '@mui/material'
import axios from 'axios';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';

function AddBlog() {
  const navigate=useNavigate()
  const [inputs,setInputs]=useState({
    title:"",description:"",imageURL:""
  });




const handleChange=(e)=>{

  setInputs((prevState)=>({
    ...prevState,
    [e.target.name]:e.target.value
  }))

};

const sendRequest=async()=>{
  const res=await axios.post("http://localhost:3000/api/blog/add",{
    title:inputs.title,
    description:inputs.description,
    image:inputs.imageURL,
    user:localStorage.getItem("userId")
  }).catch((err)=>console.log(err));
  const data=await res.data;
  return data;
}


const handleSubmit=(e)=>{ 
  e.preventDefault();
  console.log(inputs);
  sendRequest().then((data)=>console.log(data)).then(()=>navigate('/myBlogs'))

}


  return (
    <div>
      <form onSubmit={handleSubmit}>
        <Box border={1} borderColor={'gray'} borderRadius={10} boxShadow={'10px 10px 20px #ccc'} padding={3} margin={'auto'} marginTop={4}  display={'flex'} flexDirection={'column'} width={'80%'}>
          <Typography fontWeight={'bold'} padding={3} color={'grey'} variant='h4' textAlign={'center'}>Post Your Blog</Typography>
          <InputLabel    >Title</InputLabel>
          <TextField  onChange={handleChange}  name='title'  o value={inputs.title} margin='auto' variant='outlined'  />
          <InputLabel sx={{mt:2}} >Description</InputLabel>
          <TextField  onChange={handleChange} name='description' value={inputs.description} margin='auto' variant='outlined' />
          <InputLabel sx={{mt:2}} >ImageURL</InputLabel>
          <TextField  onChange={handleChange} name='imageURL' value={inputs.imageURL} margin='auto' variant='outlined' />
       <Button  sx={{mt:2,borderRadius:4,width:800,alignSelf:'center'} } variant='contained'  type='submit' >Submit</Button>
        </Box>
      </form>
    </div>
  )
}

export default AddBlog